@import url(https://fonts.googleapis.com/css?family=Kanit:900);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background: #f6f6f6;
  min-height: 100%;
}

body {
  background: transparent;
  padding-top: 66px;
}

.App {
  text-align: center;
}

.image-buttons-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  margin: 50px auto 150px;
}

.image-button-wrapper {
  align-items: stretch;
  box-sizing: border-box;
  display: flex;
  flex: 0 0 25%;
  justify-content: center;
  padding: 5px;
  position: relative;
}

.image-button-wrapper:before {
  content: "";
  display: table;
  padding-top: 100%;
}

.image-button {
  align-items: center;
  background: #fff;
  border: none;
  border-radius: 5px;
  box-shadow: 0 6px 15px rgba(36, 37, 38, 0.08);
  cursor: pointer;
  display: flex;
  flex-grow: 1;
  justify-content: center;
  padding: 5px;
  transition: box-shadow 0.25s ease 0s;
  -webkit-transition: box-shadow 0.25s ease 0s;
}

.image-button:hover {
  box-shadow: 5px 12px 20px rgba(36, 37, 38, 0.13);
}

@media (min-width: 992px) {
  .image-button-wrapper {
    padding: 0.8rem;
  }

  .image-button {
    border-radius: 16px;
    padding: 16px;
  }
}

.navbar {
  background: #f6f6f6;
  border-bottom: 2px solid #f6f6f6;
  box-shadow: 0px 1px 1px #ddd;
  font-size: calc(15px + 14 * (100vw - 320px) / 2000);
  padding: 20px 0px;
  position: fixed;
  top: 0px;
  width: 100%;
}

.navbar a {
  color: #000;
  font-weight: 600;
}

.spotlight {
  background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
  color: #fff;
  padding: 50px 0px;
  text-align: right;
}

.spotlight h1 {
  font-family: "Kanit", sans-serif;
  font-size: 30px;
  margin-bottom: 0px;
}

@media (min-width: 992px) {
  .spotlight h1 {
    font-size: 50px;
    margin-left: 40%;
    padding: 60px;
  }
}

